import React, { FC, Fragment, useContext } from 'react';
import { AppContext } from '../app';
import Street from './street';

const Streets: FC = () => {
	const context = useContext(AppContext);

	if (!context.state.showStreets) {
		return null;
	}

	return (
		<Fragment>
			<Street x={370} y={235} rotate>Goodwin</Street>
			<Street x={485} y={310} rotate>Lincoln</Street>
			<Street x={90} y={330} rotate>First</Street>

			<Street x={160} y={58}>White</Street>
			<Street x={420} y={105}>Springfield</Street>
			<Street x={410} y={170}>Green</Street>
			<Street x={300} y={340}>Gregory</Street>
		</Fragment>
	);
}
export default Streets;
