import React, { ChangeEvent, FC, useContext } from 'react';
import { Actions } from '../../reducer';
import '../../scss/zoom.scss';
import { ZoomLevel } from '../../types/zoomLevel';
import { AppContext } from '../app';

type Range = 1 | 2 | 3 | 4 | 5 | 6;

const valueMap: { [key in Range]: ZoomLevel } = {
	1: 1,
	2: 1.5,
	3: 2,
	4: 4,
	5: 8,
	6: 16
};

const zoomMap: { [key in ZoomLevel]: Range } = {
	1: 1,
	1.5: 2,
	2: 3,
	4: 4,
	8: 5,
	16: 6
};

const Zoom: FC = () => {
	const context = useContext(AppContext);

	function onChange(event: ChangeEvent<HTMLInputElement>): void {
		const zoomLevel = valueMap[parseInt(event.target.value) as Range];
		context.dispatch({ type: Actions.SetZoomLevel, zoomLevel })
	}

	return (
		<div className="zoom">
			<input
				type="range"
				onChange={onChange}
				min={1}
				max={6}
				step={1}
				value={zoomMap[context.state.zoomLevel]}
			/>
			<p className="zoom-level">
				<span className="num">{context.state.zoomLevel}</span><span className="x">x</span>
			</p>
		</div>
	);
}

export default Zoom;
