import React, { FC, useContext } from 'react';
import { Actions } from '../../reducer';
import '../../scss/picker.scss';
import { AppContext } from '../app';
import Category from './category';
import PointsOfInterest from './pointsOfInterest';
import Toggle from './toggle';
import Zoom from './zoom';

const Picker: FC = () => {
	const context = useContext(AppContext);

	function streetChange(showStreets: boolean): void {
		context.dispatch({ type: Actions.SetShowStreets, showStreets });
	}

	function labelChange(showLabels: boolean): void {
		context.dispatch({ type: Actions.SetShowLabels, showLabels });
	}

	return <div className="picker">
		<ul className="category-list">
			{
				context
					.state
					.pointOfInterestCategories
					.map(c => <li className="category" key={c.name}><Category {...c} /></li>)
			}
		</ul>
		<Zoom />
		<div className="toggles">
			<Toggle checked={context.state.showStreets} changeCallback={streetChange}>Show Streets</Toggle>
			<Toggle checked={context.state.showLabels} changeCallback={labelChange}>Show Labels</Toggle>
		</div>
		<PointsOfInterest />
	</div>
}

export default Picker;
