import React, { ChangeEvent, FC, KeyboardEvent } from 'react';
import '../../scss/toggle.scss';

interface Props {
	checked: boolean;
	changeCallback: (checked: boolean) => void;
	children: string;
}

const Toggle: FC<Props> = (props: Props) => {

	function onChange(event: ChangeEvent<HTMLInputElement>): void {
		props.changeCallback(event.target.checked);
	}

	function onKeyUp(event: KeyboardEvent): void {
		if (event.key === ' ' || event.key === 'Enter') {
			props.changeCallback(!props.checked);
		}
	}

	return (
		<label className="toggle" tabIndex={0} onKeyUp={onKeyUp}>
			<input type="checkbox" checked={props.checked} onChange={onChange} />
			<i className="material-icons">{props.checked ? 'toggle_on' : 'toggle_off'}</i>
			{props.children}
		</label>
	);
};

export default Toggle;
