import React, { CSSProperties, FC, useContext } from 'react';
import '../../scss/map.scss';
import { AppContext } from '../app';
import FullScreenButton from './fullScreenButton';
import Labels from './labels';
import Landmarks from './landmarks';
import Poi from './poi';
import Routes from './routes';
import Streets from './streets';

const Map: FC = () => {
	const context = useContext(AppContext);

	const isZoomed = context.state.zoomLevel > 1;
	const overflow = isZoomed ? 'auto' : 'hidden';

	const containerStyle: CSSProperties = context.state.fullScreen ? {
		position: 'fixed',
		top: 0,
		right: 0,
		bottom: 0,
		left: 0,
		overflow
	} : { overflow };

	const svgStyle: CSSProperties = {
		transform: isZoomed ? `scale(${context.state.zoomLevel})` : 'none',
		transformOrigin: 'top left',
		fillRule: 'evenodd',
		clipRule: 'evenodd',
		strokeLinecap: 'round',
		strokeMiterlimit: 1.5
	};

	return (
		<div className="map" style={containerStyle}>
			<svg
				xmlns="http://www.w3.org/2000/svg"
				viewBox="0 0 740 665"
				style={svgStyle}
			>
				<Streets />
				<Landmarks />
				<Routes />
				<Labels />
				<Poi />
			</svg>
			<FullScreenButton />
		</div>
	);
};

export default Map;
